/* eslint-disable */
import 'rc-tabs/assets/index.less';
import React from 'react';
import Sortable from 'react-sortablejs';
import ReactDOM from 'react-dom';
import Tabs, { TabPane } from 'rc-tabs';
import TabContent from 'rc-tabs/lib/SwipeableTabContent';
import InkTabBar from 'rc-tabs/lib/InkTabBar';

const PanelContent = ({ id }) => (
  <div>{[1, 2, 3, 4].map(item => <p key={item}>{id}</p>)}</div>
);

class Demo extends React.Component {
  state = {
    tabsData: [
      { tab: "tab1", content: "tab1 content" },
      { tab: "tab2", content: "tab2 content" }
    ]
  };

  handleRemoveTabsChild = () => {
    this.setState({ tabsData: [] });
    // 模拟网络请求
    setTimeout(() => {
      this.setState({
        tabsData: [
          { tab: "tab1", content: "tab1 content" },
          { tab: "tab2", content: "tab2 content" }
        ]
      });
    }, 100);
  };

  render() {
    return (
      <div>
        <button onClick={this.handleRemoveTabsChild}>刷新</button>
        {/* 当 tabPosition 为 'left'/'right' 时，点刷新按钮报错 */}
        <Tabs
            defaultActiveKey="3"
            tabBarPosition="left"
            renderTabBar={() => (
              <InkTabBar
                onTabClick={this.onTabClick}
                tabBarGutter={40}
              />
            )}
            renderTabContent={() => <TabContent />}
            onChange={this.onChange}
          >
            {this.state.tabsData.map(({ tab, content }) => (
              <TabPane
                tab={tab}
                key={tab}
              >
                {content}
              </TabPane>
            ))}
          </Tabs>
      </div>
    );
  }
}

ReactDOM.render(<Demo />, document.getElementById('__react-content'));
/* eslint-enable */



